var app=angular.module('Ulibi');
app.factory('UlibiApi',function($http){
    var urlBase = '/ulibi/api/';
    var h=$http;
    var api= {
        article: {
            postfix: 'article'
        }
    };
    api.article.getAll = function(){
        return h.get(urlBase+this.postfix+'/all');
    };

    return api;
});